<template>
  <view class="card">
    <view class="title">
      <view class="card-title">{{ title }}</view>
      <view class="card-price">{{ price }}</view>
    </view>
    <view class="card-address"><uni-icons type="location-filled"></uni-icons>
      {{ address }}
    </view>
    <view class="card-date"> <uni-icons type="calendar-filled"></uni-icons> {{ date }} {{ time }}</view>
    <view class="footer">
      <view class="card-status"><uni-icons type="personadd-filled"></uni-icons> {{ status }} {{ participants }} /
        {{ plan }}</view>
      <view class="card-tags"> {{ tags }} </view>
    </view>
    <button class="card-button" @click="onSignUp">去报名</button>
  </view>
</template>

<script setup>
  import {
    defineProps,
    defineEmits
  } from 'vue';

  const props = defineProps({
    title: String,
    price: String,
    address: String,
    date: String,
    time: String,
    status: String,
    participants: Number,
    plan: Number,
    tags: String
  });

  const emit = defineEmits(['sign-up']);

  const onSignUp = () => {
    emit('sign-up', props.title);
  };
</script>

<style scoped>
  .card {
    background: #fff;
    padding: 20rpx;
    margin: 20rpx;
    border-radius: 10rpx;
    font-size: 30rpx;
    line-height: 30px;
    box-shadow: 0 2rpx 4rpx rgba(0, 0, 0, 0.1);
  }

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10rpx;
  }

  .card-title {
    font-size: 36rpx;
    font-weight: bold;
  }

  .card-price {
    font-size: 36rpx;
    color: #FF3B30;
  }

  .card-button {
    background: #007AFF;
    color: #fff;
    margin-top: 10rpx;
  }
  .footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .card-tags{
    color: #007AFF;
    background-color: aqua;
    border-radius: 5px;
    line-height: 30px;
    width: 50px;
    text-align: center;
  }
</style>